<template>
    <div class="login-body">
        <navbar></navbar>
        <div class="login-component">
        <h2 class="header">Nazwa użytkownika</h2>
        <div class="username">
            <input type="text" placeholder="Watson">
        </div>
        <h2 class="header">Hasło</h2>
        <div class="password">
            <input type="password">
        </div>
        <div class="disclaimer">
            <p>Dla celów demonstracyjnych powyższe pola przyjmują dowolne wartości</p>
        </div>
        <div class="buttons">
            <router-link to="/" tag="div" class="log-in-btn btn" @click.native='loggedIn'>Zaloguj</router-link>
            <router-link to="/" tag="div" class="cancel-btn btn">Anuluj</router-link>
        </div>

        </div>
        
        
        
    </div>
</template>

<script>

import {eventBus} from '../main.js';
import Navbar from './Navbar.vue';

export default {
    components: {
        Navbar
    },
    methods: {
        loggedIn() {
            
            
            eventBus.$emit('loggedIn', true);
           
        }
    },
    beforeRouteEnter(to, from, next) {
        if (eventBus.logged) {
            
            next('/');
        } else {
            next()
        }
    }
}
</script>

