<template>
    <div>
    <app-navbar></app-navbar>
      <app-header></app-header>
      <div class="post-container">
<div class="post-wrap">
        <app-post class="app-posts" v-for="(post, index) in posts" :key='index' :posts="posts" :post="post" :num="post.postNumber" v-scrollanimation>
       
       <template v-slot:post-image>
         <!-- <img class="post-image" :src="require('../assets' + post.postImage )" alt=""> -->
       </template>
       <template v-slot:post-title>
         <h1 >{{post.postTitle}}</h1>
       </template>
       <template v-slot:post-body>
         <p>{{post.postBody}}</p>
       </template>
       <template v-slot:post-date >
         <span>{{post.postDate}}</span>
         <span>Posted by {{post.postAuthor}}</span>
         
       </template>
     </app-post>
      </div>

      </div>
      
     
     <app-footer></app-footer>
     
  </div>
</template>

<script>

import Navbar from './Navbar.vue';
import Header from './Header.vue';
import Post from './Post.vue';
import Footer from './Footer.vue';
import { eventBus } from '../main';


export default {
  components: {
    'app-header': Header,
    'app-navbar': Navbar,
    'app-post': Post,
    'app-footer': Footer
  },
  data() {
    return {
      posts: []
    }
  },
  created() {
    this.posts = eventBus.posts;
    eventBus.$on('addPost', () => {
     
     this.posts = eventBus.posts;
    } )
    eventBus.$on('editPost', (data) => {
     this.posts = data;
      
    })
    
    
  }
  
}
</script>

<style>
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  background-color: #111111;
}

.app-posts {
  display: grid;
  grid-template-rows: 5fr;
}


.post-wrap {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

@media (min-width: 500px) {
  .post-wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
      
  }
}

@media (min-width: 700px) {
  .post-wrap {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
      
  }
}

@media (min-width: 1300px) {
  .post-wrap {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
      
  }
}

@media (min-width: 2000px) {
    .post-container {
        display: grid;
    }
    .post-wrap {
        width: 80%;
        justify-self: center;
        grid-template-columns: repeat(5, 1fr);
    }
    
}


/* animate on scroll - directive classes */

.before-enter {
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.5s ease-in;
  transition-delay: 0.3s;
}

.enter {
  opacity: 1;
  transform: translateY(0);
  
}
</style>
