<template>

    
    <div>
       
        <div class="post-item">
            
                <slot  name="post-image" >
                    <img class="post-image" :src="require('../assets' + post.postImage )" alt="" :key=keys[0]>
                </slot>
            
            <div class="post-title" :key=keys[1]>
                <slot name="post-title"></slot>
            </div>
            <div class="post-body" :key=keys[2]>
                <slot name="post-body"></slot>
            </div>
          

            <router-link tag="button" class="read-more-btn" @click.native="readMore" to="postDetail">Czytaj dalej</router-link>
            <div class="post-date">
                <slot name="post-date"></slot>
                
            </div>
        </div>
    </div>

</template>

<script>
import { eventBus } from '../main';


export default {
    
    props: ['num', 'posts', 'post'],
    data() {
        return {
            keys: [1,2,3]
        }
    },
    methods: {
        readMore() {
            eventBus.selectedNumber = this.num;
            
             

            eventBus.posts.forEach( (post) => {
            if (post.postNumber == eventBus.selectedNumber) {
                
                    eventBus.selectedPost = post;
                }
                
                 
            } )
           
            
            eventBus.$emit("readPost", eventBus.selectedPost)
        }
    },
    created() {
     this.posts = eventBus.posts;
    eventBus.$on('addPost', () => {
     
     this.posts = eventBus.posts;
    } )
    eventBus.$on('editPost', (data) => {
     this.posts = data;
    
    });
    
        
    }
    
};

</script>

<style scoped>

.post-item {
    
    display: grid;
    margin: 1rem 2rem;
    justify-items: Center;
    font-family: 'Open Sans', sans-serif;
    grid-template-columns: 1fr;
    
}



/* media for large screens */



.post-image {
    width: 100%;
    max-width: 100%;
    height: auto;
    border-radius: 10px;

}



.post-title {
    padding: 0.4rem;
    margin: 1rem 0;
    color: #fff;
    border-bottom: 1px solid #FFC857;
    align-self: flex-start;
   width: 100%;

}

.post-title h1 {
    text-align: center;
     font-size: 1.5rem;
}

.post-body {
    color: #fff;
    margin-bottom: 1.5rem;
    text-align: justify;
    
}

.post-date {
    display: flex;
    justify-content: space-between;
    color: #333;
    width: 100%;
    margin: 1rem 0;
    border-top: 1px solid #333;
    border-bottom: 1px solid #333;
    font-size: .6rem;
    padding: 1rem;
    align-self: flex-end;
}

.read-more-btn {
    background-color: #FFC857;
    padding: .5rem;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
    border: none;
    font-weight: 600;
    cursor: pointer;
    transition: background-color .5s ease-in;
    align-self: flex-end;

}

button:focus { outline: none; }



.read-more-btn:hover {
    transition: background-color .5s ease-in;
    background-color: #E9724C;
}
</style>
