import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ScrollAnimation from './directives/scrollanimation.js'

Vue.directive('scrollanimation', ScrollAnimation);

export const eventBus = new Vue({
  data: {
    logged: false,
    newPost: {},
    posts: [{postNumber: 1, postAuthor: 'Watson', postDate: '12.12.2019', postTitle: 'Nowy zeszyt Mademoiselle Holmes', postImage: '/zeszytHolmes.jpg', postBody: 'To jest blog Mademoiselle Holmes, najlepszej detektyw po tej stronie rzeki Mississippi i na świecie! Ma na swoim koncie wiele rozwiązanych spraw, takich jak "Tajemnica Lukrecji", "Zaginiony Zeszyt, którego nigdy nie znalazłam", albo "Kto śpi ludziom po domach?" (ostatnia okazała się niezwykle trudną).'},
    {postNumber: 2, postAuthor: 'Watson', postDate: '14.12.2019', postTitle: 'Nowa szminka Mademoiselle Holmes', postImage: '/lipstik.jpg', postBody: 'Mademoiselle Holmes ma nową szminkę! Nie rozmazuje się nawet wtedy, gdy słynna detektyw je grzankę prosto z podłogi! Fotorelacja (wraz z degustacją owej grzanki) i recenzja już teraz na jej kanale na YouTube! Zostawcie łapkę w górę, suba, pamiętajcie o powiadomieniach i kupcie jej Mercedesa!'},
    {postNumber: 3, postAuthor: 'Watson', postDate: '16.12.2019', postTitle: 'Ulubione sushi Mademoiselle Holmes', postImage: '/sushi.jpg', postBody: 'Być może wszystkim wydaje się, że Mademoiselle Holmes je stare biszkopty i grzanki z podłogi! Nic bardziej mylnego. Zaraz wam udowodnię, że się mylicie, i opowiem o jej ulubionym sushi barze, żeby wszyscy stalkerzy wiedzieli gdzie przychodzić, aby móc ją jeszcze bardziej męczyć...'},
    {postNumber: 4, postAuthor: 'Watson', postDate: '18.12.2019', postTitle: 'Nowa sprawa Mademoiselle Holmes', postImage: '/holmesCoat.jpg', postBody: 'Zagadka, która na pewno pomoże odbudować zrujnowaną reputację Holmes! Nie będzie to jednak tak łatwe, jak wydłubywanie lukrecji z czekolady (co Holmes uparcie nazywa lukrecjanizmem), czy maczanie palców w Monte. To sprawa kryminalna z prawdziwego zdarzenia! Czy uda się jej rozwikłać choć jedną?'},
    {postNumber: 5, postAuthor: 'Watson', postDate: '20.12.2019', postTitle: 'Mademoiselle Holmes - zamyślona i dalekosiężna', postImage: '/dalekosiezna.jpg', postBody: 'Mademoiselle Holmes, gotowa by zwalczać zbrodnie, a już na pewno nie opychać się boczkiem, naleśnikami, jajecznicą i Monte, przyjmuje nowe zlecenia! Prosimy tylko o coś łatwego, bo Holmes nie chce się przemęczać przed świętami. Musi jeszcze kupić komuś kubek na prezent Bożonarodzeniowy. To bardzo ważne.'}
    
    ],
    nextNum: 5,
    selectedNumber: null,
    selectedPost: {}
  }
});

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
