<template>
    <div class="navbar">
        <p v-if="logged" class="logged">Zalogowany jako Watson</p>
        
        <div class="nav-btn" @click='openMenu' :class="{'active': menu}">
            <div class="btn-line btn-line-1"></div>
            <div class="btn-line btn-line-2"></div>
            <div class="btn-line btn-line-3"></div>
            

        </div>
        <transition name="message">
            <div v-if="message" class="message">
            <p>{{messageText}}</p>
        </div>

        </transition>
        
        
        <transition name='slide'>
            <div class="sidebar" v-show="menu">
            <ul class='sidebar-list'>
                <router-link tag="li" @click.native="closeMenu" to="/" class='list-item'>Strona główna</router-link>
                <router-link class='list-item' to="about">O Mademoiselle Holmes</router-link>
                <router-link class='list-item' to="kontakt">Kontakt</router-link>
                <router-link class='list-item' v-if="!logged" to="login" tag="li" @click.native="closeMenu">Zaloguj</router-link>
                <router-link class='list-item' to="addPost" v-if="logged" @click.native="closeMenu" tag="li">Dodaj post</router-link>                
                <router-link class='list-item' to="/" v-if="logged" @click.native="logOff" tag="li">Wyloguj</router-link>
                
                
            </ul>
        </div>
     </transition>


        

    </div>
</template>

<script>
import {eventBus} from '../main.js';
import { setTimeout } from 'timers';


export default {
    data() {
        return {
            menu: false,
            logged: false,
            message: false,
            messageText: ""
        }
    },
    methods: {
        openMenu() {
            this.menu = !this.menu
        },
        closeMenu() {
            this.menu = !this.menu
        },
        logOff() {
            eventBus.logged = false;
            this.message = true;
            this.messageText = "Wylogowano pomyślnie"
            setTimeout(() => {
                this.message = false;
                this.messageText = ""
            }, 2000)
            
            this.menu = !this.menu;
            this.logged = eventBus.logged;
        }
        
    },
    created() {
        this.logged = eventBus.logged
        eventBus.$on('loggedIn', (data) => {
            this.logged = data;
            eventBus.logged = data;
            this.message = true;
            this.messageText = "Zalogowano pomyślnie jako Watson";
            setTimeout(() => {
            this.message = false;
            this.messageText = "";
            }, 1500)
        })
        
        
        
    }
}
</script>

<style>
.message {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    background: rgba(17, 17, 17, 0.7);
    padding: 1rem;
    z-index: 100;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
        
}

@media (max-width: 600px) {
    .message {
        font-size: 1rem;
        white-space: nowrap;
        text-justify: center;
    }
}

.logged {
    color: white;
    font-size: .7rem;
    font-family: 'Open Sans', sans-serif;
    padding: .2rem;
    position: absolute;
    left: 1px;
    top: 1px;
    
    

}
.navbar {
    height: 40px;
    background: #111111;
    
    
    
}

a {
    text-decoration: none;
}

.nav-btn {
    position: fixed;
  z-index: 3;
  right: 15px;
  top: 15px;
  cursor: pointer;
    transition: transform .5s ease-in;
  
}

.btn-line {
    height: 2px;
    width: 25px;
    background-color: #FFC857;
    margin-bottom: 5px;
    transition: all .5s ease-in;
}
/* sidebar */

.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(17, 17, 17, 0.9);
    display: grid;
    align-content: center;
    z-index: 1;
    
}

.sidebar-list {
    display: grid;
    justify-items: center;
    list-style: none;
}



.list-item {
    color: #fff;
    margin: 1rem 0;
    font-size: 2rem;
    font-family: 'Open Sans', sans-serif;
    cursor: pointer;
    transition: color .5s ease-in;
  
    
}

@media (max-width: 600px) {
    .list-item {
        font-size: 1.3rem;
    }
}

.list-item:hover {
    color: #FFC857;
    transition: color .5s ease-in;
}


/* slide in transition */



.slide-enter {
   
    
transform: translateX(100%) !important;



}

.slide-enter-active {
    transition: transform .5s ease-in;
transform: translateX(0);    

}

.slide-leave {
transform: translateX(0);    

}

.slide-leave-active {
transition: transform 1s ease-in;
transform: translateX(100%);
}


/* hamburger animation */

.nav-btn.active {
    transition: transform .5s ease-in;
    transform: rotate(-180deg) translateY(-5px);
}

.nav-btn.active .btn-line-1 {
    transform: translateY(16px) rotate(45deg);
    transition: transform .5s ease-in;
    
}

.nav-btn.active .btn-line-2 {
    transition: opacity .5s ease-in;
    opacity: 0;
   
}

.nav-btn.active .btn-line-3 {
    transition: transform .5s ease-in;
    transform: translateY(2px) rotate(-45deg);
    
}

/* message transition */

.message-enter {
    opacity: 0 !important;   
    transition: all .5s ease-in;
    
}

.message-enter-active {
    opacity: 1;     
    transition: all .5s ease-in;
      
}

.message-leave {
    transition: all .5s ease-in;
    
}

.message-leave-active {
    opacity: 0;    
    transition: all .5s ease-in;
    
}

</style>
