<template>
<div>
    <navbar></navbar>
    <div class="contact-body">
        <transition name="message">
            <div v-if="message" class="message">
            <p>{{messageText}}</p>
        </div>

        </transition>
        <div class="post-grid">

        
        <div class="title">
            <h2>Kontakt</h2>
            <p>Wyślij do mnie wiadomość. Czytam wszystkie jak leci. Słowo.</p>
            <h4>Imię*</h4>
            <input type="text" v-model="name" maxlength="80">
            
        </div>
       
        <div class="introduction">
            <h4>Email*</h4>
            
            <input type="text" v-model="email" maxlength="80">
        </div>
        <div class="post">
            <h4>Treść*</h4>
            
            <textarea v-model="mailMessage" cols="30" rows="10"></textarea>
            <p>Pola oznaczone * są wymagane.</p>
        </div>
        <div class="buttons">
            <li to="/" tag="div" class="log-in-btn btn" @click='send'>Wyślij</li>
            <router-link to="/" tag="div" class="cancel-btn btn">Powrót</router-link>
        </div>
       </div>
    </div>
</div>
    
</template>


<script>


import Navbar from './Navbar.vue';
import { setTimeout } from 'timers';





export default {
    components: {
        Navbar
    },

    data() {
        return {
            message: false,
            messageText: "",
            name: "",
            email: "",
            mailMessage: ""
            

        }
    },
    methods: {
        send() {
            if (!this.name || !this.email || !this.mailMessage) {
                 this.messageText = "Przed wysłaniem uzupełnij wszystkie wymagane pola!"
                this.message = true;
                setTimeout(() => {
                this.message = false;
                this.messageText = "";
            }, 2000)
            } else if ( !this.email.includes("@", ".") ) {
               this.messageText = "Podaj prawidłowy adres email!"
                this.message = true;
                setTimeout(() => {
                this.message = false;
                this.messageText = "";
            }, 2000)
            } else {
                this.messageText = "Wysłano pomyślnie";
                this.message = true;
                this.name = "";
                this.email = "";
                this.mailMessage = "";
            setTimeout(() => {
                this.message = false;
                this.messageText = "";
            }, 2000)
            }
            
        }
    }
}
</script>

<style scoped>
    .contact-body {
    font-family: 'Open Sans', sans-serif;
    display: grid;
    padding: 1rem;
    position: relative;
              
    }

    @media (min-width: 700px) {
        
        .contact-body {
            align-items: center;
            justify-items: center;
            padding: 0;
            margin: 0;
        }

        .post-grid {
            width: 750px;
            max-width: 750px;
            display: grid;
            align-items: center;
            justify-content: center;
        }
    }

    

    li {
        list-style: none;
    }

    h4 {
        margin-bottom: 1rem;
        color: white;
    }

    .title p {
        
        margin-bottom: 1rem;
    
    }

    .title h2 {
        font-size: 2.5rem;
        margin-bottom: 1rem;
    }

    .title input {
        background-color: #1c1b1b;
        border: none;
        line-height: 1.5rem;
        color: white;
        outline: none;
        padding: .5rem;
        font-size: 1.2rem;
        width: 100%;
        border-radius: 10px;
        margin-bottom: 1rem;
    }

    

    .introduction input {
        font-family: 'Open Sans', sans-serif;        
        background-color: #1c1b1b;
        border: none;
        color: white;
        outline: none;
        padding: .5rem;
        width: 100%;
        line-height: 1.5rem;     
        font-size: 1.2rem;
        border-radius: 10px;
        margin-bottom: 1rem;
        
    }

    .contact-body h2,p {
        color: white;
    }
   
    .buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    
    }
    .btn {
    background-color: #FFC857;
    padding: .5rem;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
    border: none;
    font-weight: 600;
    cursor: pointer;
    transition: background-color .5s ease-in;
    margin-right: 10px;
    
}

.post h2 {
    margin-bottom: 1rem;
}

.post textarea {
    font-family: 'Open Sans', sans-serif;    
    background-color: #1c1b1b;
        border: none;
        color: white;
        outline: none;
        padding: .5rem;
        width: 100%;
        line-height: 1.5rem;     
        font-size: 1.2rem;
        border-radius: 10px;
        
}

.post p {
    font-size: .7rem;
    margin-bottom: 1rem;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-self: center;
    justify-content: center;
}
/* message transition */

.message-enter {
    opacity: 0 !important;   
    transition: all .5s ease-in;
    
}

.message-enter-active {
    opacity: 1;     
    transition: all .5s ease-in;
      
}

.message-leave {
    transition: all .5s ease-in;
    
}

.message-leave-active {
    opacity: 0;    
    transition: all .5s ease-in;
    
}

/* message styling */

.message {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    background: rgba(17, 17, 17, 0.7);
    padding: 1rem;
    z-index: 100;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
    max-width: 100%;
        
}

@media (max-width: 600px) {
    .message {
        font-size: 0.8rem;
        white-space: nowrap;
        text-justify: center;
    }
}
</style>
