<template>
    <div class="post-detail-body">
        <navbar></navbar>

        <div class="post-title">
            <h1>{{post.postTitle}}</h1>
        </div>
       <div class="post-img" v-if="image">
           <img :src="require('../assets' + post.postImage )" alt="">
       </div>
        <div class="post-subtitle">
            <h4>{{post.postBody}}</h4>
        </div>
        <div class="post-body">
            <p>{{text}}</p>
            
        </div>

        <div class="share">
             <p>Udostępnij ten post!</p>
             <div class="icons">
                  <i class="fab fa-facebook fa-2x"></i>
            <i class="fab fa-twitter fa-2x"></i>
            <i class="fab fa-youtube fa-2x"></i>
             </div>
           
        </div>
        

        <div class="buttons">
            <router-link tag="div" class="btn" to="/editPost" v-show="logged">
            Edytuj
            </router-link>
            <router-link tag = "div" to="/" class="btn">
            Powrót
            </router-link>
        </div>
     
        

    </div>
</template>


<script>

import {eventBus} from '../main.js';
import Navbar from './Navbar.vue';




export default {
    components: {
        Navbar
    },
    data() {
        return { 
            post: {},
            text: '',
            logged: false,
            image: false
        }
    },
    created() {
        this.logged = eventBus.logged
        eventBus.$on('readPost', (data) => {
            this.post = data;
            this.image = true;
            
            if (!data.postText) {
                this.text = 'lorem czipsum dolor sil amet. Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry\'s standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum. Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source.'
            } else {
                this.text = data.postText;
            }
        })
        
    }
    
}
</script>

<style scoped>
    
    .post-detail-body {
        color: white;
        font-family: 'Open Sans', sans-serif;
        display: grid;
        justify-items: center;
        padding: 1rem;
        
    }

    .buttons {
        display: flex;
        flex-direction: row;
    }
  
    .btn {
    
    min-width: 70px;
    display: flex;
    justify-content: center;
    background-color: #FFC857;
    color: black;
    padding: .5rem;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
    border: none;
    font-weight: 600;
    cursor: pointer;
    transition: background-color .5s ease-in;
    margin-bottom: 2rem;
    margin-right: .5rem;
}

.post-title {
    justify-self: flex-start;
    
    
    
}
.post-title h1 {
    margin-bottom: 2rem;
    font-size: 2.5rem;
    
}



.post-img img {
    width: 100%;
    max-width: 100%;
    height: auto;
    margin-bottom: 2rem;
    border-radius: 10px;
}

.post-subtitle {
    margin-bottom: 2rem;    
    justify-self: flex-start;
    
    
    
}

.post-subtitle h4 {
    text-align: justify;
    font-size: 1.5rem;
}



.post-body p {
        font-family: 'Open Sans', sans-serif;
        line-height: 2rem;
        margin-bottom: 2rem;
        text-align: justify;
        
    }

    .share {
        border-top: 1px solid #FFC857;
        display: grid;
        grid-template-rows: 2;
        justify-items: center;
    }

    .share p{
        font-size: 1.3rem;
    }

    .share .icons {
        margin: 2rem 0;
       
    }

    .icons .fab {
        cursor: pointer;
        margin-right: 1rem;
        padding: .1rem;
        
        transition: color .3s ease;
        
    }


    .fa-facebook:hover {
        color: #3b5998;
        transition: color .3s ease;
    }

    .fa-twitter:hover {
        color:#1DA1F2;
        transition: color .3s ease;
        
    }
    
    .fa-youtube:hover{
        color: red;
        transition: color .3s ease;
        
    }
    

@media (min-width: 700px) {
    .post-detail-body {
        width: 750px;
        margin: auto;
        justify-content: center;
        padding: 0;
    }
}

@media (max-width: 700px) {
    .post-title h1 {
        font-size: 1.9rem;
    }
    .post-subtitle h4 {
    font-size: 1.1rem;
    line-height: 1.8rem;
        
    }
    .post-body p {
        line-height: 1.8rem;
    }

}

</style>
