<template>
<div>
    <navbar></navbar>
    <div class="add-post-body">
        <transition name="message">
            <div v-if="message" class="message">
            <p>{{messageText}}</p>
        </div>

        </transition>
        <div class="post-grid">

        
        <div class="title">
            <h1>Edytuj post</h1>
            <h2>Tytuł posta</h2>
            <p>Jaki tytuł miałby post twoich marzeń?</p>
            <input type="text" v-model="postTitle" maxlength="80">
            
        </div>
        <div class="image">
            <h2>Wybierz zdjęcie</h2>
            <p>Wybierz zdjęcie, które najlepiej oddaje twoje uczucia!</p>
            <div class="image-selection">
                <img :src='require("../assets"+imageSource)' :class="{'newImage': imageEnters}">
                <div class="pick-right" @click="imageRight"></div>
                <div class="pick-left" @click="imageLeft"></div>
            </div>
            
        </div>
        <div class="introduction">
            <h2>Wstęp</h2>
            <p>Krótki wstęp do właściwej treści. Te kilka zdań, które tu wpiszesz, będą zajawką na stronie głównej!</p>
            <textarea cols="30" rows="4" v-model="intro" maxlength="300"></textarea>
        </div>
        <div class="post">
            <h2>Treść posta</h2>
            <p>Właściwa treść posta.</p>
            <textarea v-model="postBody" cols="30" rows="10"></textarea>
        </div>
        <div class="buttons">
            <router-link to="/editPost" tag="div" class="log-in-btn btn" @click.native='editPost'>Zapisz</router-link>
            <router-link to="/" tag="div" class="cancel-btn btn">Powrót</router-link>
            <router-link to="/" tag="div" @click.native="deletePost" class="btn del-btn">Usuń</router-link>
        </div>
       </div>
    </div>
</div>
    
</template>


<script>
import { setTimeout } from 'timers';
import {eventBus} from '../main.js';
import Navbar from './Navbar.vue';





export default {
    components: {
        Navbar
    },

    data() {
        return {
            timestamp: '',
            imageEnters: false,
            imgCount: 0,
            imageSource: "/addPost/image1.jpg",
            images: ["/addPost/image1.jpg", "/addPost/image2.jpg", "/addPost/image3.jpg", "/addPost/image4.jpg", "/addPost/image5.jpg"],
            postTitle: "",
            intro: "",
            postBody: "",
            date: Date.now(),
            currentPost: {},
            message: false,
            messageText: ""

        }
    },
    methods: {
        imageRight() {
            this.imageEnters = true;            
            this.imgCount++;
            if(this.imgCount >=5) {
                this.imgCount = 0;
            } else if (this.imgCount<0 ) {
                this.imgCount=0;
            }
            
            this.imageSource = this.images[this.imgCount];
            setTimeout(() => {this.imageEnters = false;}, 300)
        },
        imageLeft() {
             this.imageEnters = true;            
             this.imgCount--;
            if(this.imgCount >=5) {
                this.imgCount = 0;
            } else if (this.imgCount<0 ) {
                this.imgCount=4;
            }
            
            this.imageSource = this.images[this.imgCount];
            setTimeout(() => {this.imageEnters = false;}, 300)
        },
        editPost() {
            this.currentPost = {postNumber: '', postAuthor: 'Watson', postDate: this.timestamp, postTitle: this.postTitle, postImage: this.imageSource, postBody: this.intro, postText: this.postBody};
            this.currentPost.postNumber = eventBus.selectedNumber;
            
            eventBus.posts[eventBus.selectedNumber-1] = this.currentPost;
            eventBus.$emit('editPost', eventBus.posts)
            eventBus.$emit("readPost", eventBus.selectedPost)
            this.message = true;
            this.messageText = "Zmiany zapisano pomyślnie"
            setTimeout(() => {
                this.message = false;
                this.messageText = ""
            }, 2000)
            
           
        },
        getNow() {
                    const today = new Date();
                    const date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();
                    
                    const dateTime = date;
                    this.timestamp = dateTime;
        },
        updateForEdit() {
            this.currentPost = eventBus.selectedPost;
            this.postTitle = this.currentPost.postTitle;
            this.intro = this.currentPost.postBody
            if(this.currentPost.postText) {
                this.postBody = this.currentPost.postText;
            }
        },
        deletePost() {
            eventBus.posts.splice(eventBus.selectedNumber-1, 1);
            
        }
    },
    created() {
                setInterval(this.getNow, 1000);
                this.updateForEdit();
            },
    beforeRouteEnter(to, from, next) {
        if (eventBus.logged) {
            
            next();
        } else {
            next('/')
        }
    }
}
</script>

<style scoped>
    .add-post-body {
    font-family: 'Open Sans', sans-serif;
    display: grid;
    padding: 1rem 1rem 0 1rem;
    
              
    }

    @media (min-width: 700px) {
        
        .add-post-body {
            align-items: center;
            justify-items: center;
            padding: 0;
            margin: 0;
        }

        .post-grid {
            width: 750px;
            max-width: 750px;
            display: grid;
            align-items: center;
            justify-content: center;
        }
    }

    p {
        margin-bottom: 1rem;
    }

    .title h1 {
        font-size: 2.5rem;
        margin-bottom: 1rem;
        color: white;
    }

    .title input {
        background-color: #1c1b1b;
        border: none;
        line-height: 1.5rem;
        color: white;
        outline: none;
        padding: .5rem;
        font-size: 1.2rem;
        width: 100%;
        border-radius: 10px;
        margin-bottom: 1rem;
    }

    .introduction h2, .title h2 {
        margin: 1rem 0;

    }

    .introduction textarea {
        font-family: 'Open Sans', sans-serif;        
        background-color: #1c1b1b;
        border: none;
        color: white;
        outline: none;
        padding: .5rem;
        width: 100%;
        line-height: 1.5rem;     
        font-size: 1.2rem;
        border-radius: 10px;
        margin-bottom: 1rem;
        
    }

    .add-post-body h2,p {
        color: white;
    }
   
    

    .btn {
    background-color: #FFC857;
    padding: .5rem;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
    border: none;
    font-weight: 600;
    cursor: pointer;
    transition: background-color .5s ease-in;
    margin-right: 10px;
    
}

.image h2 {
    margin-bottom: 1rem;
}

.image-selection {
    position: relative;
}

.image-selection img {
    width: 100%;
    max-width: 100%;
    height: auto;
    border-radius: 10px;
    
}

.pick-right {
    position: absolute;
  top: 50%;
  left: 5%;
  width: 20px;
  height: 20px;
  transform: rotate(45deg) translateY(-50%) translateX(-50%);
  border-left: 3px solid white;
  border-bottom: 3px solid white;
  cursor: pointer;
  outline: none;
}

.pick-left {
     position: absolute;
  top: 50%;
  right: 5%;
  width: 20px;
  height: 20px;
  transform: rotate(45deg) translateY(-50%) translateX(-50%);
  border-right: 3px solid white;
  border-top: 3px solid white;
  cursor: pointer;
  outline: none;
  
}

.newImage {
  opacity: 0;
    animation: fadeIn 0.5s ease-in 1 forwards; 
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

.post h2 {
    margin-bottom: 1rem;
}

.post textarea {
    font-family: 'Open Sans', sans-serif;    
    background-color: #1c1b1b;
        border: none;
        color: white;
        outline: none;
        padding: .5rem;
        width: 100%;
        line-height: 1.5rem;     
        font-size: 1.2rem;
        border-radius: 10px;
        margin-bottom: 1rem;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-self: center;
    margin-bottom: 1rem;
    justify-content: center;
}

/* message styles */

.message {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    background: rgba(17, 17, 17, 0.7);
    padding: 1rem;
    z-index: 100;
    border-radius: 10px;
    font-family: 'Open Sans', sans-serif;
        
}

@media (max-width: 600px) {
    .message {
        font-size: 1rem;
        white-space: nowrap;
        text-justify: center;
    }

    .message p{
        margin-bottom: 0;
    }
}

.message-enter {
    opacity: 0 !important;   
    transition: all .5s ease-in;
    
}

.message-enter-active {
    opacity: 1;     
    transition: all .5s ease-in;
      
}

.message-leave {
    transition: all .5s ease-in;
    
}

.message-leave-active {
    opacity: 0;    
    transition: all .5s ease-in;
    
}
</style>
